'use client';

import { useState, FormEvent } from 'react';
import { useRouter } from 'next/navigation';

interface SearchBarProps {
  initialCity?: string;
  initialBudget?: { min: number; max: number };
  initialRoomType?: 'single' | 'shared' | 'entire';
  onSearch?: (filters: {
    city: string;
    budget: { min: number; max: number };
    roomType: string;
  }) => void;
}

export default function SearchBar({
  initialCity = '',
  initialBudget = { min: 0, max: 5000 },
  initialRoomType,
  onSearch,
}: SearchBarProps) {
  const router = useRouter();
  const [city, setCity] = useState(initialCity);
  const [minPrice, setMinPrice] = useState(initialBudget.min.toString());
  const [maxPrice, setMaxPrice] = useState(initialBudget.max.toString());
  const [roomType, setRoomType] = useState(initialRoomType || '');

  const handleSubmit = (e: FormEvent) => {
    e.preventDefault();
    const filters = {
      city,
      budget: {
        min: parseInt(minPrice) || 0,
        max: parseInt(maxPrice) || 5000,
      },
      roomType,
    };

    if (onSearch) {
      onSearch(filters as any);
    } else {
      const params = new URLSearchParams();
      if (city) params.append('city', city);
      if (minPrice) params.append('minPrice', minPrice);
      if (maxPrice) params.append('maxPrice', maxPrice);
      if (roomType) params.append('roomType', roomType);
      router.push(`/listings?${params.toString()}`);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="w-full">
      <div className="rounded-2xl bg-white p-4 shadow-xl shadow-gray-900/5 ring-1 ring-gray-900/5 md:p-6">
        <div className="grid grid-cols-1 gap-4 md:grid-cols-4 md:gap-6">
          <div className="md:col-span-1">
            <label htmlFor="city" className="mb-2 block text-sm font-semibold text-gray-700">
              Location
            </label>
            <div className="relative">
              <div className="pointer-events-none absolute inset-y-0 left-0 flex items-center pl-3">
                <svg className="h-5 w-5 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z" />
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 11a3 3 0 11-6 0 3 3 0 016 0z" />
                </svg>
              </div>
              <input
                id="city"
                type="text"
                value={city}
                onChange={(e) => setCity(e.target.value)}
                placeholder="Where to?"
                className="block w-full rounded-lg border-0 bg-gray-50 py-3 pl-10 pr-3 text-sm text-gray-900 ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-rose-600 sm:leading-6"
              />
            </div>
          </div>

          <div>
            <label htmlFor="roomType" className="mb-2 block text-sm font-semibold text-gray-700">
              Room Type
            </label>
            <div className="relative">
              <select
                id="roomType"
                value={roomType}
                onChange={(e) => setRoomType(e.target.value)}
                className="block w-full appearance-none rounded-lg border-0 bg-gray-50 py-3 pl-3 pr-10 text-sm text-gray-900 ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-rose-600 sm:leading-6"
              >
                <option value="">Any type</option>
                <option value="single">Single Room</option>
                <option value="shared">Shared Room</option>
                <option value="entire">Entire Place</option>
              </select>
              <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center pr-3">
                <svg className="h-5 w-5 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                </svg>
              </div>
            </div>
          </div>

          <div>
            <label htmlFor="minPrice" className="mb-2 block text-sm font-semibold text-gray-700">
              Min Price
            </label>
            <div className="relative">
              <div className="pointer-events-none absolute inset-y-0 left-0 flex items-center pl-3">
                <span className="text-sm text-gray-500">$</span>
              </div>
              <input
                id="minPrice"
                type="number"
                value={minPrice}
                onChange={(e) => setMinPrice(e.target.value)}
                placeholder="0"
                min="0"
                className="block w-full rounded-lg border-0 bg-gray-50 py-3 pl-7 pr-3 text-sm text-gray-900 ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-rose-600 sm:leading-6"
              />
            </div>
          </div>

          <div>
            <label htmlFor="maxPrice" className="mb-2 block text-sm font-semibold text-gray-700">
              Max Price
            </label>
            <div className="relative">
              <div className="pointer-events-none absolute inset-y-0 left-0 flex items-center pl-3">
                <span className="text-sm text-gray-500">$</span>
              </div>
              <input
                id="maxPrice"
                type="number"
                value={maxPrice}
                onChange={(e) => setMaxPrice(e.target.value)}
                placeholder="5000"
                min="0"
                className="block w-full rounded-lg border-0 bg-gray-50 py-3 pl-7 pr-3 text-sm text-gray-900 ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-rose-600 sm:leading-6"
              />
            </div>
          </div>
        </div>

        <div className="mt-6 flex justify-end">
          <button
            type="submit"
            className="inline-flex items-center gap-2 rounded-xl bg-rose-600 px-8 py-3 text-base font-semibold text-white shadow-lg shadow-rose-600/25 transition-all hover:bg-rose-700 hover:shadow-xl hover:shadow-rose-600/30 focus:outline-none focus:ring-2 focus:ring-rose-600 focus:ring-offset-2"
          >
            Search
            <svg className="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
            </svg>
          </button>
        </div>
      </div>
    </form>
  );
}
